var translations = {
  about: 'krlbell',
  over: 'tbb mint',
  almost: 'majdnem',
  lessthan: 'kevesebb mint'
};
var withoutSuffixes = {
  xseconds: ' msodperc',
  halfaminute: 'fl perc',
  xminutes: ' perc',
  xhours: ' ra',
  xdays: ' nap',
  xweeks: ' ht',
  xmonths: ' hnap',
  xyears: ' v'
};
var withSuffixes = {
  xseconds: {
    [-1]: ' msodperccel ezeltt',
    [1]: ' msodperc mlva',
    [0]: ' msodperce'
  },
  halfaminute: {
    [-1]: 'fl perccel ezeltt',
    [1]: 'fl perc mlva',
    [0]: 'fl perce'
  },
  xminutes: {
    [-1]: ' perccel ezeltt',
    [1]: ' perc mlva',
    [0]: ' perce'
  },
  xhours: {
    [-1]: ' rval ezeltt',
    [1]: ' ra mlva',
    [0]: ' rja'
  },
  xdays: {
    [-1]: ' nappal ezeltt',
    [1]: ' nap mlva',
    [0]: ' napja'
  },
  xweeks: {
    [-1]: ' httel ezeltt',
    [1]: ' ht mlva',
    [0]: ' hete'
  },
  xmonths: {
    [-1]: ' hnappal ezeltt',
    [1]: ' hnap mlva',
    [0]: ' hnapja'
  },
  xyears: {
    [-1]: ' vvel ezeltt',
    [1]: ' v mlva',
    [0]: ' ve'
  }
};

function translate(number, addSuffix, key, comparison) {
  var translated = addSuffix ? withSuffixes[key][comparison] : withoutSuffixes[key];

  if (key === 'halfaminute') {
    return translated;
  }

  return number + translated;
}

export default function formatDistance(token, count, options) {
  options = options || {};
  var adverb = token.match(/about|over|almost|lessthan/i);
  var unit = token.replace(adverb, '');
  var result;
  result = translate(count, options.addSuffix, unit.toLowerCase(), options.comparison);

  if (adverb) {
    result = translations[adverb[0].toLowerCase()] + ' ' + result;
  }

  return result;
}